.cat-info {
  gap: 20px;
  margin: 20px 10px 10px;
  display: flex;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.error {
  color: red;
}

.wrap {
  text-align: center;
}

.loader {
  text-align: center;
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 3px dashed #ff3d00;
  border-bottom: 3px solid rgba(0, 0, 0, 0);
  border-left: 3px dotted #ff3d00;
  border-right: 3px solid #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  border: 10px solid rgba(0, 0, 0, 0);
  border-right-color: #ff3d00;
  position: absolute;
  top: 31px;
  left: 20px;
  transform: rotate(-40deg);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

